import {
	myRequestPost
} from "@/utils/request.js";

export default {
	data() {
		return {
			themeList: [],
			themeDescription: []
		};
	},
	onLoad() {
		this.getThemes();

	},
	methods: {
		async getThemes() {
			let result = await myRequestPost("/rest/cks/api/theme/list/get");
			this.themeList = result.items;
			this.themeDescription = result.description;
		},
		goDetail(item) {
			uni.navigateTo({
				url: `/pages/themeDetails/themeDetails?themeId=${item.id}`
			})
		}
	}
}
